@media screen and (min-width: 1000px) {
  .joke-head-container__smiley-emoji-left {
    font-size: 3.5rem;
    margin: 0 5%;
  }

  .joke-head-container__headline {
    font-size: 5rem;
  }

  .joke-head-container__smiley-emoji-right {
    font-size: 3.5rem;
    margin: 0 5%;
  }

  .joke-button-container {
    margin-top: 2%;
  }

  .generateJoke {
    margin-left: 7%;
    padding: 0;
    width: 100%;
    max-width: 20%;
  }

  .generate-joke__clown-emoji-left {
    margin: 0 5% 0 5%;
    font-size: 2rem;
  }

  .generate-joke__text {
    font-size: 3rem;
    padding: 0 0.1rem;
    width: 100%;
    line-height: 2.2rem;
  }

  .generate-joke__clown-emoji-right {
    font-size: 2rem;
    margin: 0 5% 0 5%;
  }

  .addJoke {
    margin-left: 5%;
    padding: 0 0.5%;
    height: 100%;
  }

  .addJoke__star-emoji {
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0rem;
    padding: 0;
  }

  .joke-show-element {
    font-size: 1.7rem;
    line-height: 1.5rem;
    margin: 5% 5% 0 5%;
  }

  .joke-list-section__headline-star-emoji {
    font-size: 3rem;
  }

  .joke-list-section__joke-headline {
    font-size: 3.5rem;
  }

  .joke-div {
    margin: 0 10% 2.5% 25%;
    padding: 0;
    max-width: 50%;
  }

  .joke {
    font-size: 1.7rem;
    line-height: 2.5rem;
    margin: 2.5% 0 2.5% 5%;
  }

  .deleteJoke {
    padding: 0 2%;
    margin: 0 10% 0 0;
  }

  .red-points-top {
    width: 100%;
  }

  .deleteJoke__grid--red-point-top {
    margin-top: 30%;
    width: 0.5rem;
    height: 0.5rem;
  }

  .deleteJoke__garbagecan-emoji {
    font-size: 2.2rem;
  }

  .deleteJoke__grid--red-point-buttom {
    width: 0.5rem;
    height: 0.5rem;
    margin-bottom: 30%;
  }
}

